import {useState} from 'react'
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Avatar from '@mui/material/Avatar';

import {Paper, Stack, Typography, IconButton, InputBase, Box, Tabs, Tab} from '@mui/material'

import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import SearchIcon from '@mui/icons-material/Search'

import SwipeableDrawer from '@mui/material/SwipeableDrawer';
import FoodPage from './FoodPage'

import LocalDiningIcon from '@mui/icons-material/LocalDining';

import { grey, amber, } from '@mui/material/colors';

import data from './foods.json';

import { createTheme, ThemeProvider } from '@mui/material/styles';

const categories = [
  {
    text: "",
  },
  {
    text: "",
  },
  {
    text: "",
  },
  {
    text: "",
  },
  {
    text: "",
  },
  {
    text: "",
  },
  {
    text: "",
  },
  {
    text: "",
  },
]



const theme = createTheme({
  palette: {
    primary: {
      main: amber[500],
    },
  },
  typography: {
    body1: {
      letterSpacing: 2,
      fontSize: 12,
    },
    body2: {
      fontSize: 11,
    },
    body3: {
      fontSize: 20,
      fontWeight: "bold",
    }
  }
});


const avrs = {};
const stds = {};

["energy", "protein", "fat", "carb"].forEach((property, i) => {
  const avr = data.reduce((a, b) => a + b[property], 0) / data.length;
  const std = Math.sqrt(data.reduce((a, b) => (a + ((b[property] - avr) ** 2)), 0) / data.length);
  avrs[property] = avr;
  stds[property] = std;
});

export default function MyList() {
  const [foods, setFoods] = useState(data);
  const [text, setText] = useState("");
  const [categoryId, setCategoryId] = useState(0);

  const [food, setFood] = useState(null);


  const [state, setState] = useState(false);

  const handleClick = (food) => {
    setFood(food);
    setState(true);
  }



  return (
    <ThemeProvider theme={theme}>
      <Stack spacing={1} sx={{pt: 2, boxSizing: 'border-box'}}>
        <Stack direction="row" justifyContent="space-between" spacing={2} alignItems="center" sx={{px: 2, boxSizing: 'border-box'}}>
          <LocalDiningIcon sx={{fontSize: 30}} color="primary"/>
          <MyInput foods={foods} setFoods={setFoods} text={text} setText={setText}/>
        </Stack>

        <MyTabs value={categoryId} setValue={setCategoryId}/>
        <List disablePadding sx={{ width: '100%', px: 2, boxSizing: 'border-box' }}>
          {data.map((food, i) => {
            return(
              <ListItem
                disableGutters
                onClick={() => handleClick(food)}
              >
                <ListItemAvatar sx={{pr: 2}}>
                  <Avatar variant="rounded" src={food.img_url} sx={{ width: 80, height: 80, borderRadius: 4 }}/>
                </ListItemAvatar>
                <ListItemText
                  primary={
                    <Typography sx={{fontWeight: "bold"}}>
                      {food.name}
                    </Typography>
                  }
                  secondary={
                    <>
                    <Typography variant="body2" noWrap>
                      B12
                    </Typography>
                    <Typography variant="body2">
                    {food.energy} kcal
                    </Typography>
                    <Typography variant="body2">
                      <Stack direction="row" spacing={2} alignItems="center">
                        <Stack direction="row" spacing={0.5} alignItems="center">
                          <Avatar variant="rounded" sx={{fontSize: 12, width: 14, height: 14, bgcolor: amber[500]}}>P</Avatar>
                          <Typography variant="body2">{food.protein}g</Typography>
                        </Stack>
                        <Stack direction="row" spacing={0.5} alignItems="center">
                          <Avatar variant="rounded" sx={{fontSize: 12, width: 14, height: 14, bgcolor: amber[500]}}>F</Avatar>
                          <Typography variant="body2">{food.fat}g</Typography>
                        </Stack>
                        <Stack direction="row" spacing={0.5} alignItems="center">
                          <Avatar variant="rounded" sx={{fontSize: 12, width: 14, height: 14, bgcolor: amber[500]}}>C</Avatar>
                          <Typography variant="body2">{food.carb}g</Typography>
                        </Stack>
                      </Stack>
                    </Typography>
                    </>
                  }
                />
                <IconButton edge="end">
                  <ArrowForwardIosIcon />
                </IconButton>
              </ListItem>
            )
          })}
        </List>
      </Stack>
      <SwipeableDrawer
        anchor={"bottom"}
        open={state}
        onClose={() => setState(false)}
        onOpen={() => setState(true)}
        sx={{
          "& .MuiDrawer-paper": {
            borderRadius: "16px 16px 0 0"
          }
        }}
      >
        <Box className={"myDrawer"}>
          <FoodPage food={food} stds={stds}/>
        </Box>
      </SwipeableDrawer>
    </ThemeProvider>
  );
}


function MyTabs(props) {
  return (
    <Box sx={{ width: "100%", borderBottom: 1, borderColor: 'divider' }}>
      <Tabs
        value={props.value}
        onChange={(event, newValue) => props.setValue(newValue)}
        variant="scrollable"
        scrollButtons={false}
        sx={{
          minHeight: 30,
        }}
      >
        {categories.map((category) => (
          <Tab
            label={
              <Typography><b>{category.text}</b></Typography>
            }
            sx={{p: 0, minHeight: 30, minWidth: 60}}
          />
        ))}
      </Tabs>
    </Box>
  );
}


function MyInput(props) {
  return (
    <Paper
      component="form"
      elevation={0}
      sx={{ borderRadius: 5, display: 'flex', alignItems: 'center', width: '100%', bgcolor: grey[200], height: 40 }}
      onSubmit={(e) => e.preventDefault()}
    >
      <IconButton sx={{ p: '10px' }} aria-label="menu">
        <SearchIcon />
      </IconButton>
      <InputBase
        sx={{ ml: 1, flex: 1 }}
        placeholder="Search Google Maps"
        inputProps={{ 'aria-label': 'search google maps' }}
        value={props.text}
        onChange={(e) => props.setText(e.target.value)}
      />
    </Paper>
  );
}
