import {useState, useEffect} from 'react';

import {Stack, Typography, Avatar, Grid, Dialog} from '@mui/material';

import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import ImageIcon from '@mui/icons-material/Image';

import data from "./foods.json"

import { amber, } from '@mui/material/colors';


export default function FoodPage(props) {
  const {food, stds} = props;
  const [foods, setFoods] = useState();

  const [food2, setFood2] = useState();

  const [open, setOpen] = useState(false);

  const handleClickOpen = (food2) => {
    setFood2(food2)
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };


  useEffect(() => {
    if (food == null) return;

    const foods = data.map((food2) => {
      const e = food.energy + food2.energy;
      const p = food.protein + food2.protein;
      const f = food.fat + food2.fat;
      const c = food.carb + food2.carb;

      const distance = Math.sqrt(((e - 1700) / stds['energy']) ** 2 + ((p - 60) / stds['protein']) ** 2 + ((f - 50) / stds['fat']) ** 2 + ((c - 250) / stds['carb']) ** 2)
      food2.distance = distance;
      return food2;
    });

    foods.sort((a, b) => a.distance - b.distance);

    setFoods([...foods]);

  }, [food, stds]);


  return (
    <>
      <Stack spacing={1} sx={{p: 2}}>
        <Stack alignItems="center" spacing={1}>
          <Avatar src={food?.img_url} sx={{width: 60, height: 60}}/>
          <Typography variant="body3">
            {food?.name}
          </Typography>
          <Typography>
            B12
          </Typography>
        </Stack>
        <Typography variant="body3">
          
        </Typography>
        <FolderList food={food}/>
        <Typography variant="body3">
          
        </Typography>
        <Grid container>
          {foods?.map((food2) => {
            console.log(food2.distance)
            return (
              <Grid item xs={6}>
                <Stack spacing={0.5} sx={{boxSizing: "border-box", p: 0.5}} onClick={() => handleClickOpen(food2)}>
                  <img src={food2.img_url} alt="" style={{width: '100%', borderRadius: 10}}/>
                  <Typography sx={{fontWeight: "bold"}}>{food2.name}</Typography>
                  <Typography variant="body2" sx={{}}>
                     {parseInt(100 - food2.distance * 10)} %
                  </Typography>
                </Stack>
              </Grid>
            )
          })}
        </Grid>
      </Stack>
      <SimpleDialog
        open={open}
        onClose={handleClose}
        food={food2}
      />
    </>
  )
}

function SimpleDialog(props) {
  const { onClose, open, food } = props;

  const handleClose = () => {
    onClose();
  };

  if(food == null) return;

  return (
    <Dialog
      onClose={handleClose}
      open={open}
      fullWidth={true}
      maxWidth={"false"}
      sx={{
        "& .MuiDialog-paper": {
          borderRadius: 10,
          margin: 0,
          width: "95%"
        }
      }}
    >
      <Stack spacing={1} sx={{p: 2, boxSizing: 'border-box'}}>
        <Stack alignItems="center" spacing={1}>
          <Avatar src={food?.img_url} sx={{width: 60, height: 60}}/>
          <Typography variant="body3">
            {food?.name}
          </Typography>
          <Typography>
            B12
          </Typography>
        </Stack>
        <FolderList food={food}/>
      </Stack>
    </Dialog>
  );
}

