function FolderList(props) {
  const {food} = props;
  if (food == null) return;
  const items = [
    {
      text: "",
      property: "energy",
    },
    {
      text: "",
      property: "protein",
    },
    {
      text: "",
      property: "fat",
    },
    {
      text: "",
      property: "carb",
    },
  ]
  return (
    <List sx={{ width: '100%' }} disablePadding>
      {items.map((item, i) => (
        <ListItem disablePadding sx={{pb: 1}}>
          <ListItemAvatar sx={{minWidth: 40}}>
            <Avatar sx={{width: 25, height: 25, bgcolor: amber[500]}}>
              <ImageIcon sx={{fontSize: 12}}/>
            </Avatar>
          </ListItemAvatar>
          <ListItemText>
          <Stack direction="row" justifyContent="space-between">
          <Typography fontSize={13} sx={{letterSpacing: 0}}>{item.text}</Typography>
          <Typography sx={{letterSpacing: 0}}>{food[item.property]} kcal</Typography>
          </Stack>

          </ListItemText>
        </ListItem>
      ))}
    </List>
  );
}
